export const Coineta = 'coineta_287t5782g74287g782'
export const Append = '4d8cb2'
import * as axios from 'axios'

export const getAuth = async () => {
  let res = await axios.default({
    method: 'get',
    url: `https://jsonbox.io/${Coineta}`,
    headers: {
      'Content-Type': 'application/ json',
      'accept': 'application/json',
    },
  })
  return `token ${res.data[0].times}${Append}`
}



